<template>
  <Popup
    :show="props.show"
    title="Add images"
    :actions="[
      { type: 'secondary', name: 'Cancel', callback: close },
      { type: 'primary', name: 'Add images', callback: send },
    ]"
    @close="close"
  >
    <div class="addImagesPopup">
      <ImageSelector @change="onFilesChange" />
      <ul class="images">
        <li class="image" v-for="image in state.images" :key="image.data.name">
          <div class="preview">
            <img :src="image.data.src" />
          </div>
          <div class="right">
            <label>
              <p>Project ID</p>
              <input type="number" v-model="image.projectID" />
            </label>
            <label>
              <p>Started at</p>
              <input type="datetime-local" v-model="image.from" />
            </label>
            <label>
              <p>Finished at</p>
              <input type="datetime-local" v-model="image.to" />
            </label>
          </div>
        </li>
      </ul>
    </div>
  </Popup>
</template>

<script setup>
import api from '@/api'
import { loadImage } from '@/lib'
import { ImageSelector, Popup } from '@/components'

const { t } = useI18n()

const props = defineProps({
  show: {
    type: Boolean,
    default: false
  }
})

const emits = defineEmits(['close'])

const state = reactive({
  loading: false,
  images: []
})

const onFilesChange = async files => {
  const images = []
  for (let file of files)
    images.push({
      data: await loadImage(file),
      projectID: 0,
      from: "",
      to: "",
    })
  console.log(images)
  state.images = images.sort((a, b) => a.data.date - b.data.date)
}

const close = () => {
  console.log(state.images)
  emits('close')
}

const send = async () => {
  state.loading = true
  try {
    await api.addSession({ type: 't' })
  }
  catch (error) {
    console.log('error', error)
  }
  state.loading = false
}
</script>

<style lang="scss" scoped>
.addImagesPopup {
  padding: 32px;
  > .images {
    > .image {
      display: flex;
      position: relative;
      border: 2px solid $dark2;
      overflow: hidden;
      list-style: none;
      > .preview {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 200px;
        height: 200px;
        > img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
      > .right {
        padding: 16px;
        > label {
          > p {
          }
          > input {
          }
        }
      }
      &:not(:first-child) {
        margin-top: -2px;
      }
      &:first-child {
        margin-top: 32px;
        border-radius: 8px 8px 0 0;
      }
      &:last-child {
        border-radius: 0 0 8px 8px;
      }
    }
  }
}
</style>
